<template>
  <div class="formMapView">
    <div class="viewName">
      <label for="viewName__input">View name</label>
      <input id="viewName__input" value="Default" />
      <svg v-if="!editedMapView" class="close-btn" width="7" height="7">
        <path fill="#fff" d="M-1-1h9v9h-9z" />
        <g>
          <path
            stroke-linecap="undefined"
            stroke-linejoin="undefined"
            stroke-width="1.5"
            stroke="#000"
            fill="none"
            d="M.75.75l5 5M.75 5.75l5-5"
          />
        </g>
      </svg>
    </div>
    <div class="formMapView__header">
      Overlay image
    </div>
    <div class="formMapView__sectionDynamic">
      <!-- if-new map-view -->

      <div v-if="editedMapView" class="newView">
        <div class="imageUploader">
          <div class="text">Drag image here to upload</div>
        </div>
      </div>

      <!-- if-edit-map view -->

      <div v-if="!editedMapView" class="editView">
        <select
          class="selected-image-name"
          name="cadImages"
          id="cadImageSelect"
          @change="cadImageSelected"
        >
          <option v-for="cadImageName in cadImageNames" :key="cadImageName">
            {{ cadImageName }}
          </option>
        </select>
        <div class="selected-image-coordinates">
          <div />
          <div><label>lat</label></div>
          <div><label>lng</label></div>
          <svg height="24" width="24">
            <circle
              cx="12"
              cy="12"
              r="10"
              stroke="#3388FF"
              stroke-width="3"
              fill="transparent"
            />
          </svg>
          <input v-model="localTopLeftLat" @blur="coordinatesUpdated()" />
          <input v-model="localTopLeftLng" @blur="coordinatesUpdated()" />
          <svg height="24" width="24">
            <circle
              cx="12"
              cy="12"
              r="10"
              stroke="#FFE74C"
              stroke-width="3"
              fill="transparent"
            />
          </svg>
          <input v-model="localTopRightLat" @blur="coordinatesUpdated()" />
          <input v-model="localTopRightLng" @blur="coordinatesUpdated()" />
          <svg height="24" width="24">
            <circle
              cx="12"
              cy="12"
              r="10"
              stroke="#FF5964"
              stroke-width="3"
              fill="transparent"
            />
          </svg>
          <input v-model="localBottomLeftLat" @blur="coordinatesUpdated()" />
          <input v-model="localBottomLeftLng" @blur="coordinatesUpdated()" />
        </div>

        <div class="actions">
          <!--          <button disabled class="button" @click="coordinatesUpdated()">-->
          <!--            Save-->
          <!--          </button>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FormMapView",
  props: {
    cadImageNames: Array,
    topLeftLat: Number,
    topLeftLng: Number,
    topRightLat: Number,
    topRightLng: Number,
    bottomLeftLat: Number,
    bottomLeftLng: Number
  },
  created() {
    this.localTopLeftLat = this.topLeftLat;
    this.localTopLeftLng = this.topLeftLng;
    this.localTopRightLat = this.topRightLat;
    this.localTopRightLng = this.topRightLng;
    this.localBottomLeftLat = this.bottomLeftLat;
    this.localBottomLeftLng = this.bottomLeftLng;
  },
  data() {
    return {
      editedMapView: undefined,
      localTopLeftLat: 53.5606529,
      localTopLeftLng: 9.9761438,
      localTopRightLat: 53.5606529,
      localTopRightLng: 9.9783825,
      localBottomLeftLat: 53.5586262,
      localBottomLeftLng: 9.9761438
    };
  },
  methods: {
    coordinatesUpdated() {
      this.$emit("coordinatesUpdated", {
        topLeft: [+this.localTopLeftLat, +this.localTopLeftLng],
        topRight: [+this.localTopRightLat, +this.localTopRightLng],
        bottomLeft: [+this.localBottomLeftLat, +this.localBottomLeftLng]
      });
    },
    cadImageSelected(e) {
      this.$emit("cadImageSelected", e.target.value);
    }
  },
  watch: {
    topLeftLat(newValue) {
      this.localTopLeftLat = newValue;
    },
    topLeftLng(newValue) {
      this.localTopLeftLng = newValue;
    },
    topRightLat(newValue) {
      this.localTopRightLat = newValue;
    },
    topRightLng(newValue) {
      this.localTopRightLng = newValue;
    },
    bottomLeftLat(newValue) {
      this.localBottomLeftLat = newValue;
    },
    bottomLeftLng(newValue) {
      this.localBottomLeftLng = newValue;
    }
  }
};
</script>

<style lang="scss" src="./FormMapView.scss" scoped />
