<script src="./node_modules/leaflet-imageoverlay-rotated/Leaflet.ImageOverlay.Rotated.js"></script>
<script>
import { createPopper } from "@popperjs/core";
import "./helpers/arrays.js";
import tippy from "tippy.js";
import "tippy.js/dist/tippy.css";
import { saveAs } from "file-saver";
import FormMapView from "./components/FormMapView/FormMapView";
//import geojson2svg, { Renderer } from "geojson-to-svg";
import rotated from "leaflet-imageoverlay-rotated";
// import cadImageA0B0 from "./assets/cadPlans/OMR20 CAD A0+B0 groundplan DRAFT_200303.jpg";
// import cadImageA1 from "./assets/cadPlans/OMR20 CAD A1 groundplan DRAFT_200304-1.jpg";
// import cadImageA3 from "./assets/cadPlans/OMR20 CAD A3 groundplan DRAFT_200303-1.jpg";
// import cadImageA4 from "./assets/cadPlans/OMR20 CAD A4 groundplan DRAFT_200304-1.jpg";
// import cadImageAEM from "./assets/cadPlans/OMR20 CAD AEM groundplan DRAFT_200303-1.jpg";
// import cadImageB10F from "./assets/cadPlans/OMR20 CAD B1 0F groundplan DRAFT_200302-1.jpg";
// import cadImageB11F from "./assets/cadPlans/OMR20 CAD B1 1F groundplan DRAFT_200302-1.jpg";
// import cadImageB30F from "./assets/cadPlans/OMR20 CAD B3 0F groundplan DRAFT_200302-1.jpg";
// import cadImageB40F from "./assets/cadPlans/OMR20 CAD B4 0F groundplan DRAFT_200303-1.jpg";
// import cadImageB41F from "./assets/cadPlans/OMR20 CAD B4 1F groundplan DRAFT_200303-1.jpg";
// import cadImageB5 from "./assets/cadPlans/OMR20 CAD B5 groundplan DRAFT_200302-1.jpg";
// import cadImageB7 from "./assets/cadPlans/OMR20 CAD B7 groundplan DRAFT_200303-1.jpg";
// import cadImageEO0F from "./assets/cadPlans/OMR20 CAD EO 0F groundplan DRAFT_200303-1.jpg";
// import cadImageEO1F from "./assets/cadPlans/OMR20 CAD EO 1F groundplan DRAFT_200303-1.jpg";
import cadA1 from "./assets/cadPlans/A1.jpg";
import cadA3 from "./assets/cadPlans/A3.jpg"
import cadA4 from "./assets/cadPlans/A4.jpg";
import cadB1B4_1floor from "./assets/cadPlans/B1-B4_1floor.jpg";
import cadB1B4_ground from "./assets/cadPlans/B1-B4_groundfloor.jpg";
import B5 from "./assets/cadPlans/B5.jpg";
import B6 from "./assets/cadPlans/B6.jpg";
import B7 from "./assets/cadPlans/B7.jpg";


const backend = "https://pyrite-phrygian-barge.glitch.me/"

import { v4 as uuidv4 } from "uuid";
function generateGetBoundingClientRect(x = 0, y = 0) {
  return () => ({
    width: 0,
    height: 0,
    top: y,
    right: x,
    bottom: y,
    left: x
  });
}

export default {
  name: "App",
  components: { FormMapView },
  data() {
    return {
      // CONFIG
      serveGeojson: 1,
      geojsonUrl: `${backend}geojson`,
      // APP STATE
      //polygon selection and edit modal
      editPolygonModal: {
        id: null,
        type: null, // layer type -> bg | hall | booth | markers
        hall: null,
        color: null,
        text: null,
        image: null,
        myomrCompanyName: null,
        customLabel: null,
      },
      importOpen: false,
      selectedPolygon: null,
      polygonSelected: false,
      selectedPolygonPoint: { x: 300, y: 300 },
      //states
      toolmode: "", // move, cut, edit, delete, marker, line, polygon
      visibleLayer: {
        marker: true,
        booth: true,
        hall: true,
        background: true
      },
      originalJson: [],
      markerColors: {
        topLeft: "#3388FF",
        topRight: "#FFE74C",
        bottomLeft: "#FF5964"
      },
      saveSuccessful: null,
      cadImages: [
        {
          name: "OMR22 A1",
          image: cadA1
        },
        {
          name: "OMR22 A3",
          image: cadA3
        },
        {
          name: "OMR22 A4",
          image: cadA4
        },
        {
          name: "OMR22 B1-B4 groundfloor",
          image: cadB1B4_ground
        },
        {
          name: "OMR22 B1-B4 1st floor",
          image: cadB1B4_1floor
        },
        {
          name: "OMR22 B5",
          image: cadB5
        },
        {
          name: "OMR22 B6",
          image: cadB6
        },
        {
          name: "OMR22 B7",
          image: cadB7
        }

      ],
      imageLayers: [
        {
          id: "9b1deb4d-3b7d-4bad-9bdd-2b0d7b3dcb6d",
          imageFile: cadA4,
          name: "Default",
          visible: true,
          coords: {
            topLeft: [53.5606529, 9.9761438],
            topRight: [53.5606529, 9.9783825],
            bottomLeft: [53.5586262, 9.9761438]
          }
        }
      ]
    };
  },
  computed: {
    cadImageNames() {
      return this.cadImages.map(cadImage => cadImage.name);
    }
  },
  created() {
    this.map = null;
    this.virtualElement = {
      getBoundingClientRect: generateGetBoundingClientRect()
    };
  },
  async mounted() {
    //var polygonSelected;
    // create the map

    let popper = document.querySelector(".popup");
    this.instance = createPopper(this.virtualElement, popper, {
      modifiers: [{ name: "offset", options: { offset: [0, 18] } }]
    });

    //tooltips on the toolbar
    tippy(document.querySelectorAll(".toolbar .button[data-tippy-content]"), {
      placement: "right"
    });
    const drawImageLayers = () => {
      this.map.createPane("cadOverlayPane");
      this.imageLayers.forEach(layer => {
        //markers
        this.initCadImageHandles(
          layer.id,
          layer.coords,
          "topLeft",
          this.markerColors.topLeft
        );
        this.initCadImageHandles(
          layer.id,
          layer.coords,
          "topRight",
          this.markerColors.topRight
        );
        this.initCadImageHandles(
          layer.id,
          layer.coords,
          "bottomLeft",
          this.markerColors.bottomLeft
        );
        this.updateCadImage(layer);
      });
    };
    const initMap = async () => {
      this.map = new window.L.map("map").setView([53.560074, 9.978011], 15);

      window.L.tileLayer(
        "https://api.mapbox.com/styles/v1/mapbox/dark-v10/tiles/256/{z}/{x}/{y}?access_token=pk.eyJ1IjoibWFya3VzLW9tciIsImEiOiJjanRmaml1OTUwYXVzNGFtaHNmMGdiOTR3In0.kT-uNoSoWBmKrCJkjlAbjg",
        {
          maxZoom: 22,
          attribution:
            'Map data &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors, ' +
            '<a href="http://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, ' +
            'Imagery © <a href="http://mapbox.com">Mapbox</a>'
        }
      ).addTo(this.map);

      this.renderImportedGeoJson();
      drawImageLayers();

      // add leaflet-geoman controls with some options to the map
      this.map.pm.addControls({
        position: "bottomleft",
        drawCircle: false,
        drawMarker: true,
        drawRectangle: false
      });

      this.map.pm.setPathOptions({
        color: "#D83364",
        fillColor: "#D83364",
        fillOpacity: 0.4
      });

      // add onclick function to each existing layer
      this.map.pm.findLayers().forEach(layer => {
        layer.on("pm:drag", e => {
          if (e.target.feature.properties.type === "cadHandle") {
            this.saveimageCoords(e);
          } else {
            this.openPopup(e);
          }
        });
      });

      //
      // created event
      //
      this.map.on("pm:create", e => {
        e.layer.on("click", e => {
          console.log(
            "clicked a created polygon",
            e.target.feature.properties.id
          );
          console.log("dragging", this.dragging);
          this.openPopup(e);
          //e.target.feature.properties.id = "wow2";
        });
        e.layer.feature = {};
        e.layer.feature.type = "Feature";
        e.layer.feature.properties = { id: "" };
        e.layer.feature.properties.type = this.toolmode;
        if (["polygon", "background", "hall", "booth"].includes(this.toolmode))
          this.toolmode = "";
      });
      this.map.on("pm:dragstart", () => {
        //set drag mode to true so the properties modal doen not open
        console.log("dragging started");
        this.dragging = true;
      });
      this.map.on("pm:dragend", () => {
        //set drag mode to true so the properties modal doen not open
        console.log("dragging ended");
        this.dragging = false;
      });
    };
    await this.loadCurrentState();
    setTimeout(initMap, 100);
  },
  methods: {
    async loadCurrentState() {
      if (this.serveGeojson) {
        return fetch(this.geojsonUrl)
          .then(response => {
            if (response.ok) return response.json();
            else {
              console.log("could not load geojson", response)
              throw new Error("Could not load GEOJSON");
            }
          })
          .then(json => {
            console.log(json);
            this.originalJson = this.originalJson.concat([json]);
          });
      } else {
        this.importOpen = true;
        return
      }
    },
    importGeojson() {
      var files = document.getElementById("selectFiles").files;
      if (files.length <= 0) return false;
      var fr = new FileReader();
      fr.onload = e => {
        var result = JSON.parse(e.target.result);
        this.originalJson = this.originalJson.concat([result]);
        this.importOpen = false;
        this.renderImportedGeoJson();
      };
      fr.readAsText(files.item(0));
    },
    renderImportedGeoJson() {
      this.originalJson.forEach(featuregroup => {
        window.L.geoJSON(featuregroup, {
          style: {
            weight: 0,
            fillOpacity: 1
          }
        }).addTo(this.map);
      });
      // order the layers
      this.orderButton();

      // update popup triggers
      this.map.pm.findLayers().forEach(layer => {
        layer.on("drag", e => {
          console.log("dragging marker", e.target);
          this.openPopup(e);
        });

        layer.on("click", e => {
          console.log(
            "clicked a existing/imported polygon",
            e.target.feature.properties.id
          );
          this.openPopup(e);
        });
      });
    },
    saveimageCoords(e) {
      //update coords in the data properties
      console.log("updating cadImage", e);
      const handle = e.target;
      const imageLayer = this.imageLayers.find(
        image => image.id === handle.feature.properties.viewId
      );
      imageLayer.coords[handle.feature.properties.position] = [
        handle._latlng.lat,
        handle._latlng.lng
      ];
      this.updateCadImage(imageLayer);
    },
    updateProperties() {
      // read from input fields
      this.selectedPolygon.target.feature.properties.id = this.editPolygonModal.id;
      this.selectedPolygon.target.feature.properties.hall = this.editPolygonModal.hall;
      this.selectedPolygon.target.feature.properties.type = this.editPolygonModal.type;
      this.selectedPolygon.target.feature.properties.color = this.editPolygonModal.color;
      this.selectedPolygon.target.feature.properties.text = this.editPolygonModal.text;
      this.selectedPolygon.target.feature.properties.image = this.editPolygonModal.image;
      this.selectedPolygon.target.feature.properties.myomrCompanyName = this.editPolygonModal.myomrCompanyName;
      this.selectedPolygon.target.feature.properties.customLabel = this.editPolygonModal.customLabel;
      this.polygonSelected = false;
    },
    openPopup(e) {
      if (this.toolmode !== "") return;
      console.log("opening popup", e);
      this.polygonSelected = true;
      let popupCoords = { x: e.containerPoint.x, y: e.containerPoint.y };
      this.selectedPolygonPoint.x = popupCoords.x;
      this.selectedPolygonPoint.y = popupCoords.y;
      this.virtualElement.getBoundingClientRect = generateGetBoundingClientRect(
        popupCoords.x,
        popupCoords.y
      );
      this.instance.update();
      this.selectedPolygon = e;

      // clear fields
      let clearFields = fields => {
        fields.forEach(field => {
          this.editPolygonModal[field] = "";
        });
      };
      let updateField = (fields, values) => {
        fields.forEach(field => {
          if (values[field]) this.editPolygonModal[field] = values[field];
        });
      };
      clearFields(["id", "color", "hall", "type", "text", "image"]);
      updateField(
        ["id", "color", "hall", "type", "text", "image"],
        e.target.feature.properties
      );
    },

    openImport() {
      this.importOpen = true;
      this.originalJson = [];
    },
    async exportFeatures() {
      const filteredLayers = this.map.pm.findLayers().filter(layer => {
        if (layer.properties) {
          return layer.properties.type !== "cadHandle";
        }
        return true;
      });
      const features = filteredLayers.map(layer => {
        console.log(layer.toGeoJSON());
        return layer.toGeoJSON();
      });
      let data = { type: "featuresCollections", features };
      // eslint-disable-next-line no-constant-condition
      if (this.serveGeojson) {
        const response = await fetch(this.geojsonUrl, {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify(data)
        })
          .then(resp => resp.json())
         // validate is save worked
        if(response.success){
          // save worked
          this.saveSuccessful = true;
        }else{
          //save failed
          this.saveSuccessful = false
        }
        setTimeout(()=>{
          this.saveSuccessful = null;
        }, 3000)

      } else {
        var blob = new Blob([JSON.stringify(data)], {
          type: "text/plain;charset=utf-8"
        });
        saveAs(blob, "features.json");
      }
    },
    updateCadImage(imageLayerProps) {
      this.map.eachLayer(layer => {
        //delete layer if is cadpane
        if (layer.feature) {
          if (layer.feature.properties.id === imageLayerProps.id) {
            this.map.removeLayer(layer);
          }
        }
      });
      const image = window.L.imageOverlay
        .rotated(
          imageLayerProps.imageFile,
          imageLayerProps.coords.topLeft,
          imageLayerProps.coords.topRight,
          imageLayerProps.coords.bottomLeft,
          { className: "cad-image", pane: "cadOverlayPane" }
        )
        .addTo(this.map);
      image.feature = { properties: { id: imageLayerProps.id } };
    },
    initCadImageHandles(viewId, coords, position, color) {
      const marker = window.L.circleMarker(coords[position], {
        radius: 10,
        color
      }).addTo(this.map);
      marker.feature = {
        properties: {
          type: "cadHandle",
          color,
          position,
          viewId
        }
      };
    },
    updateCadImageHandles(viewId, coords, position) {
      console.log("updating", viewId, coords, position);
      const markerToRemove = this.map.pm
        .findLayers()
        .find(
          marker =>
            marker.feature.properties.type === "cadHandle" &&
            marker.feature.properties.viewId === viewId &&
            marker.feature.properties.position === position
        );

      console.log("iterating over marker", markerToRemove);
      console.log(
        "removing marker",
        markerToRemove,
        markerToRemove.feature.properties
      );
      this.map.removeLayer(markerToRemove);
      const markerColor = this.markerColors[position];
      console.log(
        "removed marker color was",
        markerColor,
        "coords were",
        coords
      );
      const newMarker = window.L.circleMarker(coords, {
        radius: 10,
        color: markerColor
      }).addTo(this.map);
      newMarker.feature = {
        properties: {
          type: "cadHandle",
          color: markerColor,
          position,
          viewId
        }
      };
      newMarker.on("pm:drag", e => this.saveimageCoords(e));
    },

    // clicks the leaflet toolbutton and checks if it got activated
    toolButton({ toolmode, leafletselector }) {
      document.querySelector(leafletselector).click();
      setTimeout(() => {
        let active = document
          .querySelector(leafletselector)
          .parentElement.parentElement.classList.contains("active");
        this.toolmode = active ? toolmode : "";
      }, 200);
    },

    toggleLayer(layerType) {
      this.map.pm.findLayers().forEach(layer => {
        if (layer.feature.properties.type === layerType)
          if (this.visibleLayer[layerType])
            layer.getElement().style.display = "none";
          else layer.getElement().style.display = "block";
      });

      this.visibleLayer[layerType] = !this.visibleLayer[layerType];
    },

    orderButton() {
      console.log("ordering the layers");
      let order = ["marker", "booth", "hall", "background"];
      order.forEach(layerType => {
        this.map.pm.findLayers().forEach(layer => {
          console.log("ordering layer:", layer);
          if (layer.feature.properties.type === layerType) {
            if (layer.feature.properties.type !== "marker") {
              layer.bringToBack();
              // set color to the property one otherwise default for layer
              layer.feature.properties.fillOpacity = 1;
              if (
                layer.feature.properties.color === undefined ||
                layer.feature.properties.color === ""
              ) {
                layer.setStyle({ fillColor: "blue" });
              } else {
                layer.setStyle({ fillColor: layer.feature.properties.color });
              }
            }
          }
        });
      });
    },

    centerMap(lat, lng) {
      this.map.panTo({ lat, lng });
    },
    coordinatesUpdated(coordinates) {
      const updatedMapView = this.imageLayers[0];
      updatedMapView.coords = Object.assign({}, coordinates);
      this.updateCadImageHandles(
        updatedMapView.id,
        updatedMapView.coords.topLeft,
        "topLeft"
      );
      this.updateCadImageHandles(
        updatedMapView.id,
        updatedMapView.coords.topRight,
        "topRight"
      );
      this.updateCadImageHandles(
        updatedMapView.id,
        updatedMapView.coords.bottomLeft,
        "bottomLeft"
      );
      this.updateCadImage(updatedMapView);
    },

    cadImageSelected(cadImageName) {
      const updatedMapView = this.imageLayers[0];
      const imageFile = this.cadImages.find(
        cadImage => cadImage.name === cadImageName
      ).image;
      updatedMapView.imageFile = imageFile;
      this.updateCadImage(updatedMapView);
    },

    toggleEdit(id) {
      this.map.pm.findLayers().forEach(layer => {
        if (layer.feature.properties.id && layer.feature.properties.id === id) {
          layer.pm.toggleEdit({ allowSelfIntersection: false });
          this.polygonSelected = false;
        }
      });
    }
  }
};
</script>
